body.is-dragging {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

body.is-dragging > li {
  background-color: #dadada;
  list-style: none;
  -webkit-transition: background-color .2s;
  -o-transition: background-color .2s;
  transition: background-color .2s;
}

body.is-dragging .remove-btn {
  visibility: hidden !important;
}



